@import "src/commun/styles/sass/mixins";

.button {
  /*@include button($primaryColor, $fontFamily, 16px, 0s, 12px 34px, 30px, 1px solid transparent, 10px, 10px, relative, 1);*/
  background-color: $primaryColor;
  font-family: $fontFamily;
  font-size: 16px;
  transition-delay: 0s;
  padding: 12px 34px;
  border-radius: 30px;
  border: 1px solid transparent;
  margin-left: 10px;
  margin-right: 10px;
  position: relative;
  z-index: 3;

}